<script>

import KetThuc from '../components/KetThuc.vue'
import Modau from '../components/Modau.vue'
import { Clicks } from '../data/Click'
import LeftCom from '../components/Lefts.vue'
import RightCom from '../components/Right.vue'
export default {
  // eslint-disable-next-line vue/multi-word-component-names
  name: 'chuyen-phat-gia-muon-tien',
  data: () => ({
    anh: 'https://cotich.net/uploads/news/a_1919821834_chuyen-phat-gia-muon-tien.jpg',
    per: -1,
    next: -1,
    ht: -1,
    ht2: -1,
    arrClassScc: ["", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", ""],
    kkk: 1
  }),
  metaInfo: {
    title: 'Chuyện phật gia: Mượn tiền? - Qùa Tặng Cuộc Sống',
    meta: [
      { name: 'thumbnail', content: 'https://cotich.net/uploads/news/a_1919821834_chuyen-phat-gia-muon-tien.jpg' },
      { charset: 'utf-8' },
      { name: 'Keywords', content: 'Chuyện phật gia: Mượn tiền?' },
      { name: 'Description', content: 'Thực tế trong cuộc sống, thí chủ ai cũng đều sẽ ít nhất bị hỏi vay tiền một vài lần. Nhưng đa số đều không thể chối từ vì nhiều lý do khác nhau. Điều khó làm nhất là gì? Là vay mượn! Thí chủ tất nhiên sẽ luôn coi người cho mình...' },

      { itemprop: 'name', content: 'Qùa Tặng Cuộc Sống' },
      { name: 'viewport', content: 'width=device-width, initial-scale=1, maximum-scale=1' },
      { name: 'language', content: 'Vietnamese,vn' },
      { name: 'geo.region', content: 'VN' },
      { property: 'article:tag', content: 'Qùa Tặng Cuộc Sống, Qùa Tặng Cuộc Sống' },
      { name: 'robots', content: 'index,follow' },
      { name: '', content: '' },
    ]
  },
  components: {
    LeftCom,
    RightCom,
    Modau,
    KetThuc
  },
  methods: {
    denmodau() {
      Clicks.clickmodau();
    },
    denketthuc() {
      Clicks.clickketthuc();
    },
    clicks(vts) {
      this.ht = vts + 1;
      this.ht2 = vts;
      this.per = vts - 1;
      this.next = vts + 1;
      if (this.ht % 2 === 0) {
        this.arrClassScc[this.ht2] = this.setClass(this.arrClassScc[this.ht2], false);
        if (this.per > 0) {
          this.arrClassScc[this.per] = this.setClass(this.arrClassScc[this.per], false);
        }
      } else {
        this.arrClassScc[this.ht2] = this.setClass(this.arrClassScc[this.ht2], true);
        if (this.next < this.arrClassScc.length) {
          this.arrClassScc[this.next] = this.setClass(this.arrClassScc[this.next], true);
        }
      }
      this.kkk++;
    },
    setClass(sClass, add) {
      var f = 'flipped';
      if (add) {
        if (sClass.indexOf(f) < 0) {
          sClass = sClass + " " + f;
        }
      } else {
        if (sClass.indexOf(" " + f) > 0) {
          sClass = sClass.replace(" " + f, "");
        } else
          if (sClass.indexOf(f) > 0) {
            sClass = sClass.replace(f, "");
          }
      }
      return sClass;
    },
    getCl(vt) {
      return this.arrClassScc[vt];

    },
    getZindex(vt) {
      if (vt % 2 === 0) {
        return "z-index: " + (4 - vt) + ";"
      } else {
        return "";
      }
    }
  },
  created() {
  }
}
</script>

